import {
  IonButton,
  IonButtons,
  IonCard,
  IonCol,
  IonDatetime,
  IonGrid,
  IonIcon,
  IonInput,
  IonLabel,
  IonModal,
  IonRow,
} from "@ionic/react";
import { createId, getToday } from "common/functions";
import { Types } from "common/types";
import { addCircleOutline, push } from "ionicons/icons";
import { useState } from "react";
import { CardProps, ContentChangeEvent } from ".";
import Calendar from "components/calendar";

import "./card.scss";
import "./calendar.scss";

interface TmpDateObject {
  id: string;
  title: string;
  date: string;
  description?: string;
  time?: string;
}

const CalendarSplitColumnCard = (props: CardProps) => {
  const onContentChange = props.onContentChange ?? ((e) => {});

  const [modalIsOpen, setModalIsOpen] = useState<boolean>(false);
  const [addBtnDisabled, setAddBtnDisabled] = useState<boolean>(false);
  const [dateDetail, setDateDetail] = useState<Types.CalendarPlate[]>([]);

  const [editDate, setEditDate] = useState<TmpDateObject>({
    id: createId("(Sin título)"),
    title: "(Sin título)",
    date: getToday(),
  });

  const triggerOnContentChangeEvent = (value: Types.CalendarPlate[]) => {};

  if (props.type === "normal") return <></>;
  if (props.readonly) {
    return (
      <>
        <IonCard>
          <IonGrid>
            {props.content.map((date: Types.CalendarPlate) => (
              <IonRow key={date.id} className="text-size-sm">
                <IonCol size="5">
                  <IonDatetime
                    className="ion-no-padding"
                    value={date.date}
                    max="2121-12-31"
                    displayFormat="DD/MM/YYYY HH:mm"
                    disabled={props.readonly}
                  />
                </IonCol>

                <IonCol className="flex-row row-start">
                  <div className="w-100 text-color-white">
                    {date.title}
                    <span className="text-color-dark text-truncate">
                      {date.description}
                    </span>
                  </div>
                </IonCol>
              </IonRow>
            ))}
          </IonGrid>
        </IonCard>
      </>
    );
  } else {
    return (
      <>
        <IonModal
          isOpen={modalIsOpen}
          cssClass="card-modal text-color-white"
          onDidDismiss={(e) => setModalIsOpen(false)}
        >
          <div className="mv-lg">
            <IonLabel className="mh-lg">Añadir un evento</IonLabel>
            <IonGrid>
              <IonRow className="mh-lg mv-md">
                <IonInput
                  value={
                    editDate.title === "(Sin título)" ? "" : editDate.title
                  }
                  inputMode="text"
                  placeholder="Título del evento"
                  autofocus
                  required
                  onIonChange={(e) => {
                    editDate.title = e.detail.value!;
                    setEditDate(editDate);
                  }}
                />
              </IonRow>
              <IonRow className="mt-lg mh-lg">
                <IonDatetime
                  className="ion-no-padding"
                  value={editDate.date}
                  max="2121-12-31"
                  displayFormat="D de MMMM, YYYY"
                  monthNames="enero, febrero, marzo, abril, mayo, junio, julio, agosto, septiembre, octubre, noviembre, diciembre"
                  disabled
                />
              </IonRow>
            </IonGrid>
            <Calendar
              className="mv-sm calendar"
              value={editDate.date}
              onChange={(d) => {
                const test = {
                  id: editDate.id,
                  title: editDate.title,
                  date: d.toISOString(),
                  description: editDate.description,
                  time: editDate.time,
                };
                setEditDate(test);
              }}
            />
          </div>
        </IonModal>
        <IonCard>
          {dateDetail.length === 0 ? (
            <></>
          ) : (
            <IonGrid>
              {dateDetail.map((detail, index) => (
                <IonRow key={detail.id} className="text-size-sm">
                  <IonCol size="5">
                    <IonDatetime
                      className="ion-no-padding"
                      value={detail.date}
                      max="2121-12-31"
                      displayFormat="DD/MM/YYYY HH:mm"
                      disabled={props.readonly}
                    />
                  </IonCol>

                  <IonCol className="flex-row row-start">
                    <div className="w-100 text-color-white">
                      {detail.title}
                      <span className="text-color-dark text-truncate">
                        {detail.description}
                      </span>
                    </div>
                  </IonCol>
                </IonRow>
              ))}
            </IonGrid>
          )}
          <IonButtons className="flex-row row-center ion-padding-vertical">
            <IonButton
              onClick={() => {
                setModalIsOpen(true);
              }}
              disabled={addBtnDisabled}
            >
              <IonIcon icon={addCircleOutline} size="large" color="primary" />
            </IonButton>
          </IonButtons>
        </IonCard>
      </>
    );
  }
};

export { CalendarSplitColumnCard };
