import React from "react";
import {
  IonMenu,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonList,
  IonItem,
  IonMenuToggle,
  IonIcon,
  IonLabel,
  IonRow,
  IonGrid,
  IonCol,
} from "@ionic/react";
import { arrowForward, close } from "ionicons/icons";
import "./menu.scss";
import { hash32 } from "common/functions";

interface MenuEntry {
  title: string;
  url: string;
  icon?: string;
}

const menuEntries: MenuEntry[] = [];

const MenuItem = ({ data }: { data: MenuEntry }) => {
  let icon = data.icon === undefined ? arrowForward : data.icon;
  let selected = window.location.pathname === data.url ? "selected " : "";

  return (
    <IonMenuToggle autoHide={false}>
      <IonItem
        className={selected + "menu-item"}
        routerLink={data.url}
        routerDirection="none"
        lines="none"
        detail={false}
      >
        <IonLabel>{data.title}</IonLabel>
        <IonIcon
          slot="end"
          ios={icon}
          md={icon}
          className="ion-padding-end"
          color="white"
        />
      </IonItem>
    </IonMenuToggle>
  );
};

const MenuHeader = () => {
  return (
    <IonHeader>
      <IonToolbar color="red">
        <IonGrid>
          {/* Close button */}
          <IonRow className="ion-justify-content-end">
            <IonCol size="auto">
              <IonMenuToggle autoHide={false}>
                <IonIcon icon={close} size="large" color="white" />
              </IonMenuToggle>
            </IonCol>
          </IonRow>

          {/* Menu title */}
          <IonRow>
            <IonCol>
              <IonTitle className="menu-title ion-padding-vertical">
                Recetario <br />
                familiar
              </IonTitle>
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonToolbar>
    </IonHeader>
  );
};

export const Menu = () => (
  <>
    <IonMenu contentId="main-content" side="end">
      <MenuHeader />

      <IonContent>
        {/* Menu routes */}
        <IonList className="ion-no-padding">
          {menuEntries.map((entry) => (
            <MenuItem data={entry} key={hash32(entry.title)} />
          ))}
        </IonList>

        {/* Menu footer */}
        <IonItem className="menu-footer" lines="none"></IonItem>
      </IonContent>
    </IonMenu>
  </>
);
