import { useState } from "react";
import CalendarInput, { Detail } from "react-calendar";

type CalendarProps = {
  value: string;
  className: string;
  onChange: (date: Date) => void;
};

const Calendar = (props: CalendarProps) => {
  const [date, setDate] = useState<Date>(new Date(props.value));
  const [view, setView] = useState<Detail>("month");
  const [activeStartDate, setActiveStartDate] = useState<Date>(
    new Date(props.value)
  );
  var clickedYear = false;

  return (
    <>
      <div className={props.className}>
        <CalendarInput
          className="shadow"
          view={view}
          value={date}
          selectRange={false}
          activeStartDate={activeStartDate}
          onActiveStartDateChange={(props) => {
            setActiveStartDate(props.activeStartDate);
          }}
          onViewChange={(p) => {
            if (clickedYear) {
              clickedYear = false;
              p.view = "month";
            }
            switch (p.view) {
              case "year":
                setView("decade");
                break;

              default:
                setView("month");
                break;
            }
          }}
          onClickYear={(v, e) => {
            const yyyy = v.getFullYear();

            const newDate = new Date(date.valueOf());
            newDate.setFullYear(yyyy);

            setDate(newDate);
            setActiveStartDate(newDate);
            clickedYear = true;
            props.onChange(newDate);
          }}
          onClickDay={(v, e) => {
            setDate(v);
            props.onChange(v);
          }}
          formatMonthYear={(loc, d) => {
            const text = d.toLocaleDateString(loc, {
              month: "long",
              year: "numeric",
            });
            return text.replace(" de", "");
          }}
          locale="es-CL"
          calendarType="ISO 8601"
        />
      </div>
    </>
  );
};

export default Calendar;
