import {
  IonButton,
  IonButtons,
  IonCard,
  IonCol,
  IonGrid,
  IonIcon,
  IonInput,
  IonRow,
} from "@ionic/react";
import { createId } from "common/functions";
import { Types } from "common/types";
import { addCircleOutline } from "ionicons/icons";
import { useState } from "react";
import { CardProps, ContentChangeEvent } from ".";
import "./card.scss";

const NormalSplitColumnCard = (props: CardProps) => {
  const onContentChange = props.onContentChange ?? ((e) => {});

  const [addBtnDisabled, setAddBtnDisabled] = useState<boolean>(false);
  const [foodDetail, setFoodDetail] = useState<Types.FoodDetail[]>([]);

  const triggerOnContentChangeEvent = (value: Types.FoodDetail[]) => {
    // Filter array values to avoid sending empty ones
    var filteredEventValues: Types.FoodDetail[] = [];

    // Filter as well, but only remove completely empty ones
    var filteredValues: Types.FoodDetail[] = [];

    // Amount of empty input values (both id & amount)
    var numOfEmpty = 0;

    value.forEach((detail) => {
      if (detail.id === null && detail.amount === null) {
        numOfEmpty++;
      } else if (detail.id === null) {
        filteredValues.push(detail);
      } else {
        filteredEventValues.push(detail);
        filteredValues.push(detail);
      }
    });

    if (numOfEmpty > 0) {
      filteredValues.push({ id: null, name: "", amount: null });
      setAddBtnDisabled(true);
    } else {
      setAddBtnDisabled(false);
    }

    console.log(`numOfEmpty=${numOfEmpty}, values=`, value);

    onContentChange(
      new CustomEvent<ContentChangeEvent>("contentChange", {
        detail: { value: filteredEventValues },
      })
    );
    setFoodDetail(filteredValues);
  };

  if (props.type === "calendar") return <></>;
  if (props.readonly) {
    return (
      <>
        <IonCard>
          <IonGrid>
            {props.content.map((detail: Types.FoodDetail, index: number) => (
              <IonRow key={index} className="text-color-white">
                <IonCol size="10">{detail.name}</IonCol>

                <IonCol className="w-100"> {detail.amount}</IonCol>
              </IonRow>
            ))}
          </IonGrid>
        </IonCard>
      </>
    );
  } else {
    return (
      <>
        <IonCard>
          {foodDetail.length === 0 ? (
            <></>
          ) : (
            <IonGrid>
              {foodDetail.map((detail, index) => (
                <IonRow className="text-color-white" key={index}>
                  <IonCol size="9">
                    <IonInput
                      value={detail.name}
                      inputMode="text"
                      placeholder="Nombre"
                      autofocus
                      required
                      onIonChange={(e) => {
                        const value = e.detail.value!;

                        foodDetail[index].name = value;
                        foodDetail[index].id =
                          value !== "" ? createId(value) : null;
                        triggerOnContentChangeEvent(foodDetail);
                      }}
                    />
                  </IonCol>

                  <IonCol className="w-100">
                    <IonInput
                      value={detail.amount}
                      inputMode="text"
                      placeholder="Cantidad"
                      required
                      onIonChange={(e) => {
                        foodDetail[index].amount =
                          e.detail.value! !== "" ? e.detail.value! : null;
                        triggerOnContentChangeEvent(foodDetail);
                      }}
                    />
                  </IonCol>
                </IonRow>
              ))}
            </IonGrid>
          )}

          <IonButtons className="flex-row row-center ion-padding-vertical">
            <IonButton
              onClick={() => {
                const newFoodDetail = foodDetail.concat({
                  id: null,
                  refId: null,
                  name: "",
                  amount: null,
                });
                setFoodDetail(newFoodDetail);
                triggerOnContentChangeEvent(newFoodDetail);
              }}
              disabled={addBtnDisabled}
            >
              <IonIcon icon={addCircleOutline} size="large" color="primary" />
            </IonButton>
          </IonButtons>
        </IonCard>
      </>
    );
  }
};

export { NormalSplitColumnCard };
