async function fetchJson(url: string): Promise<[Response | undefined, any]> {
  const response = await fetch(url);

  if (response.ok) {
    return [response, await response.json()];
  } else {
    return [undefined, undefined];
  }
}

// s=>{for(var i=0,h=9;i<s.length;)h=Math.imul(h^s.charCodeAt(i++),9**9);return h^h>>>9}
// https://stackoverflow.com/a/52171480
const hash32 = (input: string) => {
  for (var i = 0, h = 9; i < input.length; )
    h = Math.imul(h ^ input.charCodeAt(i++), 9 ** 9);

  return h ^ (h >>> 9);
};

const createId = (input: string): string => {
  return hash32(
    hash32(input) + Math.floor(Math.random() * 100000).toString()
  ).toString();
};

const sleep = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));

const getToday = (): string => {
  var today = new Date();

  const yyyy = today.getFullYear();
  const mm = String(today.getMonth() + 1).padStart(2, "0");
  const dd = String(today.getDate()).padStart(2, "0");
  const HH = String(today.getHours()).padStart(2, "0");
  const MM = String(today.getMinutes()).padStart(2, "0");

  return `${yyyy}-${mm}-${dd}T${HH}:${MM}:00`;
};

export { fetchJson, hash32, sleep, getToday, createId };
