import { IonItemDivider, IonLabel } from "@ionic/react";
import "./card.scss";
import { NormalSplitColumnCard } from "./normal-split-column-card";
import { CalendarSplitColumnCard } from "./calendar-split-column-card";

export type ContentChangeEvent = {
  value: any[] | undefined;
};

export type CardProps = {
  type?: "normal" | "calendar";
  title?: string;
  content: any;

  separator?: boolean;
  readonly?: boolean;
  starred?: boolean;
  expanded?: boolean;

  onContentChange?: (event: CustomEvent<ContentChangeEvent>) => void;
};

const SplitColumnCard = (props: CardProps) => {
  const type = props.type ?? "normal";
  const readonly = props.readonly ?? false;
  const separator = props.separator ?? true;

  // type can only be "calendar" or "normal"
  const card =
    type === "calendar" ? (
      <CalendarSplitColumnCard
        type="calendar"
        title={props.title}
        content={props.content}
        readonly={readonly}
        separator={separator}
        onContentChange={props.onContentChange}
      />
    ) : (
      <NormalSplitColumnCard
        type="normal"
        title={props.title}
        content={props.content}
        readonly={readonly}
        separator={separator}
        onContentChange={props.onContentChange}
      />
    );

  return (
    <>
      {separator ? <IonItemDivider /> : <></>}

      {props.title !== undefined ? (
        <IonLabel className="card-title">{props.title}</IonLabel>
      ) : (
        <></>
      )}

      {card}
    </>
  );
};

const CalendarCard = (props: CardProps) => {
  return <></>;
};

const ExpandableCard = (props: CardProps) => {
  return <></>;
};

const ChecklistCard = (props: CardProps) => {
  return <></>;
};

export { SplitColumnCard, CalendarCard, ExpandableCard, ChecklistCard };
